import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  .title {
    top: 0;
    right: 0;
    margin-block-start: 0;
    margin-block-end: 0;
    text-align: left;
  }
`;

const Time = () => {
  return (
    <Container className='flex-container'>
      <div className='flex-item-1'>
        <p id='title' className='title'>
          Lost track of time
        </p>
        <div className='image-box image-2 first-left-image'>
          <img
            className='images'
            src='https://www.dropbox.com/s/rg4v4rbzn3li6w1/Time%2001.jpg?dl=1'
            alt='Time-01'
            width={800}
            height={600}
          />
          <img
            className='images hover'
            src='
            https://www.dropbox.com/s/15lthdf2l0025do/Time%2001_hover.jpg?dl=1'
            alt='Time-01'
            width={800}
            height={600}
          />
        </div>
        <img
          className='images image-3'
          src='https://www.dropbox.com/s/urwganonfdye1k9/Disconnected%2002.jpg?dl=1'
          alt='Disconnected-02'
          width={600}
          height={800}
        />
        <video
          autoPlay
          loop
          className='images image-1'
          width={800}
          height={600}
        >
          <source
            src='https://www.dropbox.com/s/nmwost8czpsc4gb/Disconnected%2004.mp4?dl=1'
            type='video/mp4'
          />
        </video>
        <div className='image-box image-2 '>
          <img
            className='images'
            src='https://www.dropbox.com/s/ibbm3njtwqppjjh/Disconnected%2006.png?dl=1'
            alt='Disconnected-06'
            width={600}
            height={800}
          />
          <img
            className='images hover'
            src='https://www.dropbox.com/s/ruuxo3viwsytu8e/Disconnected%2006_hover.png?dl=1'
            alt='Disconnected-06'
            title='Disconnected-06'
            width={600}
            height={800}
          />
        </div>
      </div>
      <div className='flex-item-2'>
        <p id='label' className='label'>
          &emsp;If you ask yourself ‘What day is it’ more often these days, you
          are not alone. Being stuck at home during the quarantine has distorted
          our sense of time. Without activity distinctions in calendar, the days
          all seem to blend together.
        </p>
        <div className='image-box image-2 first-right-image'>
          <img
            className='images hover'
            src='https://www.dropbox.com/s/280r1i57jpjvzpk/Disconnected%2003_hover.jpg?dl=1'
            alt='Disconnceted-03'
            width={800}
            height={600}
          />
          <img
            className='images'
            src='https://www.dropbox.com/s/bkyob9k4jdd283z/Disconnected%2003.png?dl=1'
            alt='Disconnected-03'
            width={800}
            height={600}
          />
        </div>
        <div className='image-box image-3'>
          <img
            className='images'
            src='https://www.dropbox.com/s/52zh5nns0n9yx0w/Disconnected%2005.jpg?dl=1'
            alt='Disconnected-05'
            width={600}
            height={800}
          />
          <img
            className='images hover'
            src='https://www.dropbox.com/s/3b73qtc8pgca91g/Disconnected%2005_hover.jpg?dl=1'
            alt='Disconnected-05'
            width={600}
            height={800}
          />
        </div>
        <div className='image-box image-1'>
          <img
            className='images'
            src='https://www.dropbox.com/s/g8xie6elnxp7aao/Disconnected%2007.png?dl=1'
            alt='Disconnected-07'
            width={600}
            height={800}
          />
          <img
            className='images hover'
            src='https://www.dropbox.com/s/mjn4uqk6pobcmrl/Disconnected%2007_hover.jpg?dl=1'
            alt='Disconnected-07'
            width={600}
            height={800}
          />
        </div>
      </div>
    </Container>
  );
};

export { Time };
